.custom-control {
	position: relative;
	display: block;
	min-height: 1.2rem;
	padding-inline-start: 1.5rem;
}
.custom-control-inline {
	display: -ms-inline-flexbox;
	display: inline-flex;
	margin-inline-end: 1rem;
}
.custom-control-input {
	position: absolute;
	z-index: -1;
	opacity: 0;
	&:checked~.custom-control-label::before {
		color: $white;
	}
	&:focus~.custom-control-label::before {
		box-shadow: 0 0 0 1px $light, 0 0 0 2px rgba(98, 58, 162, 0.25);
	}
	&:active~.custom-control-label::before {
		color: $white;
		background-color: rgba(218, 201, 232, 0.5);
	}
	&:disabled~.custom-control-label {
		color: #868e96;
		&::before {
			background-color: #e9ecef;
		}
	}
}
.custom-control-label {
	margin-bottom: 0;
	&::before {
		position: absolute;
		inset-block-start: 0.25rem;
		inset-inline-start: 0;
		display: block;
		width: 1rem;
		height: 1rem;
		pointer-events: none;
		content: "";
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		background-color: $background;
	}
	&::after {
		position: absolute;
		inset-block-start: 0.25rem;
		inset-inline-start: 0;
		display: block;
		width: 1rem;
		height: 1rem;
		content: "";
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 50% 50%;
	}
}
.custom-checkbox {
	.custom-control-label::before {
		border-radius: 3px;
	}
	.custom-control-input {
		&:checked~.custom-control-label::after {
			background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E");
		}
		&:indeterminate~.custom-control-label::after {
			background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 4'%3E%3Cpath stroke='%23fff' d='M0 2h4'/%3E%3C/svg%3E");
		}
		&:disabled {
			&:checked~.custom-control-label::before {
				background-color: rgba(163, 149, 175, 0.5);
			}
			&:indeterminate~.custom-control-label::before {
				background-color: rgba(212, 182, 228, 0.5);
			}
		}
	}
}
.custom-radio {
	.custom-control-label::before {
		border-radius: 50%;
	}
	.custom-control-input {
		&:checked~.custom-control-label::after {
			background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3E%3Ccircle r='3' fill='%23fff'/%3E%3C/svg%3E");
		}
		&:disabled:checked~.custom-control-label::before {
			background-color: rgba(163, 149, 175, 0.5);
		}
	}
}
.custom-select {
	display: inline-block;
	width: 100%;
	height: 2.375rem;
	padding-block: 0.5rem;
	padding-inline: 1.75rem;
	line-height: 1.5;
	color: #9493a9;
	vertical-align: middle;
	background: $background url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 10 5'%3E%3Cpath fill='#999' d='M0 0L10 0L5 5L0 0'/%3E%3C/svg%3E") no-repeat right 0.75rem center;
	background-size: 8px 10px;
	border: 1px solid $border;
	border-radius: 3px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	&:focus {
		outline: 0;
		box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(98, 58, 162, 0.5);
		&::-ms-value {
			color: $default-color;
			background-color: $white;
		}
	}
	&[multiple], &[size]:not([size="1"]) {
		height: auto;
		padding-right: 0.75rem;
		background-image: none;
	}
	&:disabled {
		color: #868e96;
		background-color: #e9ecef;
	}
	&::-ms-expand {
		opacity: 0;
	}
}
.custom-select-sm {
	height: calc(1.8125rem + 2px);
	padding-block: 0.5rem;
	font-size: 75%;
}
div.dataTables_wrapper div.dataTables_filter input {
	margin-inline-start: 0.5em;
	display: inline-block;
	width: auto;
	height: calc(1.8125rem + 2px);
}
.custom-select-lg {
	height: calc(2.6875rem + 2px);
	padding-block: 0.5rem;
	font-size: 125%;
}
.custom-file {
	position: relative;
	display: inline-block;
	width: 100%;
	height: 2.375rem;
	margin-bottom: 0;
}
.custom-file-input {
	position: relative;
	z-index: 2;
	width: 100%;
	height: 2.375rem;
	margin: 0;
	opacity: 0;
	&:focus~.custom-file-label {
		border-color: #1991eb;
		box-shadow: 0 0 0 2px rgba(98, 58, 162, 0.25);
		&::after {
			border-color: #1991eb;
		}
	}
	&:lang(en)~.custom-file-label::after {
		content: "Browse";
	}
}
.custom-file-label {
	position: absolute;
	inset-block-start: 0;
	inset-inline: 0;
	z-index: 0;
	height: 2.375rem;
	padding: 0.375rem 0.75rem;
	line-height: 1.5;
	color: $default-color;
	background-color: $light;
	border: 1px solid $border;
	border-radius: 3px;
	&::after {
		position: absolute;
		inset-block-start: 0;
		inset-inline-end: 0;
		z-index: 3;
		display: block;
		height: calc(2.375rem - 1px * 2);
		padding: 0.375rem 0.75rem;
		line-height: 1.5;
		color: $white;
		content: "Browse";
		border-inline-start: 1px solid $border;
		border-radius: 0 3px 3px 0;
	}
}
.custom-range {
	width: 100%;
	padding-inline-start: 0;
	background-color: transparent;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	&:focus {
		outline: none;
	}
	&::-moz-focus-outer {
		border: 0;
	}
	&::-webkit-slider-thumb {
		width: 1rem;
		height: 1rem;
		margin-block-start: -0.25rem;
		border: 0;
		border-radius: 1rem;
		-webkit-appearance: none;
		appearance: none;
		&:focus {
			outline: none;
			box-shadow: 0 0 0 1px $light, 0 0 0 2px rgba(98, 58, 162, 0.25);
		}
		&:active {
			background-color: #d4e1f4;
		}
	}
	&::-webkit-slider-runnable-track {
		width: 100%;
		height: 0.5rem;
		color: transparent;
		cursor: pointer;
		background-color: $border;
		border-color: transparent;
		border-radius: 1rem;
	}
	&::-moz-range-thumb {
		width: 1rem;
		height: 1rem;
		border: 0;
		border-radius: 1rem;
		-moz-appearance: none;
		appearance: none;
		&:focus {
			outline: none;
			box-shadow: 0 0 0 1px $light, 0 0 0 2px rgba(98, 58, 162, 0.25);
		}
		&:active {
			background-color: #d4e1f4;
		}
	}
	&::-moz-range-track {
		width: 100%;
		height: 0.5rem;
		color: transparent;
		cursor: pointer;
		background-color: $border;
		border-color: transparent;
		border-radius: 1rem;
	}
	&::-ms-thumb {
		width: 1rem;
		height: 1rem;
		border: 0;
		border-radius: 1rem;
		appearance: none;
		&:focus {
			outline: none;
			box-shadow: 0 0 0 1px $light, 0 0 0 2px rgba(98, 58, 162, 0.25);
		}
		&:active {
			background-color: #d4e1f4;
		}
	}
	&::-ms-track {
		width: 100%;
		height: 0.5rem;
		color: transparent;
		cursor: pointer;
		background-color: transparent;
		border-color: transparent;
		border-width: 0.5rem;
	}
	&::-ms-fill-lower {
		background-color: $border;
		border-radius: 1rem;
	}
	&::-ms-fill-upper {
		margin-inline-end: 15px;
		background-color: $border;
		border-radius: 1rem;
	}
}

